import log from './log';
import interfaces from './interfaces';
import { errObj } from '../../js/Msg.js';
import Vue from "vue";
/**
 * 
 * @param {any} customId
 * @param {String} filePath
 * @param {Object} picsUpLoadData
 */
export function UpLoadFile(customId, filePath, picsUpLoadData) { // : (, , )
	log(':' + JSON.stringify(picsUpLoadData));
	if (interfaces.baseUrl && filePath.indexOf(interfaces.baseUrl) != -1) { //: ,  resolve. customId, 
		const replacePath = filePath.replace(interfaces.baseUrl, '');
		return Promise.resolve({
			data: replacePath
		});
	}
	let _this = this;
	let url = '';
	let formData = {};
	let name = 'files';
	let header = {
		Authorization: uni.getStorageSync("token")
	}
	switch (customId) { //picsUpLoadFileType, urlformDataname
		case 'UpLoadImage_1': //
			url = '';
			formData = {};
			name = '';
			break;
		default: //
			url = interfaces.upLoadImg;
			formData = {
				// relateId: Vue.prototype.$store.state.currentRelateId
			};
			name = 'files';
			break;
	}
	if (!url) {
		uni.showToast({title:'url', icon: 'none'});
		return Promise.reject({...errObj['102'], errMsg: 'url'});
	}
	if (!filePath) {
		uni.showToast({title:'filePath', icon: 'none'});
		return Promise.reject({...errObj['102'], errMsg: 'filePath'});
	}
	const obj = {
		url,
		header,
		name,
		filePath
	}








	obj.formData = formData;

	return uni.uploadFile(obj);
}

/**
 * 
 * @param {String} vals
 * @param {String} val
 */
export function pics_splice(vals, val) { // , vals val
	if (typeof(vals) !== 'string') { // 
		vals = val || '|'; // 
		return vals;
	}
	vals += val ? '|' + val : '|';
	return vals; // return vals
}

function renderUrlData(u, d) {
	let c = '';
	let n = 0;
	if (d)
		for (let i in d) {
			if (n > 0)
				c += '&' + i + '=' + d[i];
			else
				c += '?' + i + '=' + d[i];
			++n;
		}
	return u + c;
}