// showToast
export const showToast = (title, type = "") => {
	uni.showToast({
		icon: type == "success" ? "success" : "none",
		title
	});
}

// showModal
export const showModal = (content, successCallback) => {
	let params = {
		title: "",
		content,
		success: (res) => {
			if (res.confirm) {
				successCallback();
			}
		}
	};
	uni.showModal(params);
}