/**
 * inputTap custom
 * @param {any} customId
 */
export function inputCustomTapFc(customId) { // returnPromise, resolveinput
	return new Promise((resolve, reject) => {
		switch (customId) {
			case 'id':
				break;
			default:
				uni.scanCode({ //, 
					success: function(res) {
						log('' + res.scanType);
						log('' + res.result);
						resolve(res.result);
					}
				});
				break;
		}
	})
}
